import styled from "styled-components";

export const Divider = styled.div`
    ${props => (props.hasOwnProperty('vert') && props.vert !== false) ? 
        `
            min-height: ${props.length || '100%'};
            width: ${props.padding || '1px'};
            margin-left: ${props.padding || '1px'};
            border-left: ${props.width || '0.8px'} ${props.color || '#838383'} solid;
        ` : 
        `
            width: ${props.length || '100%'};
            min-height: ${props.padding || '1px'};
            margin-top: ${props.padding || '1px'};
            border-top: ${props.width || '0.8px'} ${props.color || '#838383'} solid;
        `
    }
`