import React from 'react'
import Survivor from './Pages/Survivor'
import Killer from './Pages/Killer'
import { loadFromStorage, saveToStorage } from './Utils/storage';
import { getDefaultLangMap, getLangMap } from './Utils/translation';
import { Divider } from './Components/Divider';
import styled from 'styled-components';
import { AlignContainer } from './Components/AlignContainer';
import { narrowLayout } from './Utils/layout';
import { AppButton, BackButton } from './Components/AppButton';

const Root = styled(AlignContainer)`
    height: 100%;
    align-items: center;
`
const VersionLabel = styled.div`
    position: fixed;
    left: 0;
    bottom: 0;
    margin: 8px 12px;
    font-size: 16px;
`
const Home = styled.div`
    display: flex;
    flex-direction: ${props => (props.flex || 'row')};
    flex-wrap: nowrap;
`


class App extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            page: 0,
            langMap: {}
        }

        this.default_map = getDefaultLangMap()
        this.translate = this.translate.bind(this)
        this.switchPageHome = this.switchPage.bind(this, 0)
        this.switchPageSurvivor = this.switchPage.bind(this, 1)
        this.switchPageKiller = this.switchPage.bind(this, 2)
    }

    async componentDidMount() {
        const langMap = await getLangMap(loadFromStorage('lang', 'en'))
        const narrow = narrowLayout()
        this.setState({ langMap, narrow })

        window.addEventListener('resize', () => {
            const narrowNew = narrowLayout()
            if (narrowNew ^ this.state.narrow)
                this.setState({ narrow: narrowNew })
        })
    }

    changeLanguage(lang) {
        saveToStorage('lang', lang)
        this.setState({
            langMap: getLangMap(lang)
        })
    }

    translate(key, value = []) {
        let translation = ''
        if (key in this.state.langMap) {
            translation = this.state.langMap[key]
        }
        else if (key in this.default_map) {
            translation = this.default_map[key]
        }
        return value.reduce((prev, cur, idx) => prev.replaceAll(`$${idx}`, cur), translation)
    }

    switchPage(page) {
        this.setState({ page })
    }

    render() {
        return (
            <Root>
                {
                    (this.state.page === 1 || this.state.page === 2) &&
                    <BackButton back={this.switchPageHome} translate={this.translate} />
                }
                {
                    this.state.page === 0 &&
                    <Home flex={this.state.narrow ? 'column' : 'row'}>
                        <AppButton narrow={this.state.narrow} title={this.translate('misc_survivor')}
                            img="https://static.wikia.nocookie.net/deadbydaylight_gamepedia_en/images/b/b3/IconHelpLoading_survivor.png"
                            onClick={this.switchPageSurvivor}></AppButton>
                        <Divider vert={!this.state.narrow} padding="40px" />
                        <AppButton narrow={this.state.narrow} title={this.translate('misc_killer')}
                            img="https://static.wikia.nocookie.net/deadbydaylight_gamepedia_en/images/0/06/IconHelpLoading_killer.png"
                            onClick={this.switchPageKiller}></AppButton>
                    </Home>
                }
                {this.state.page === 1 && <Survivor translate={this.translate} />}
                {this.state.page === 2 && <Killer translate={this.translate} />}
                <VersionLabel>
                    {this.translate('misc_version')} 5.1.0
                </VersionLabel>
            </Root>
        )
    }
}

export default App