import axios from 'axios'

export const getLangMap = (lang) => {
    return new Promise(resolv => axios.get(`https://hm-ysjiang.github.io/DeadByDaylight-Random-Build/translations/${lang}.json`)
        .then(res => resolv(res.data)).catch(err => resolv({})))
}

export const getDefaultLangMap = () => {
    return new Promise(resolv => axios.get(`https://hm-ysjiang.github.io/DeadByDaylight-Random-Build/translations/en.json`)
        .then(res => resolv(res.data)).catch(err => resolv({})))
}
