export const loadFromStorage = (key, default_value = '') => {
    const value = localStorage.getItem('hmysj_dbdrnd_' + key)
    if (value === null || value === undefined) {
        localStorage.setItem('hmysj_dbdrnd_' + key, JSON.stringify(default_value))
        return default_value
    }
    return JSON.parse(value)
}

export const saveToStorage = (key, value) => {
    localStorage.setItem('hmysj_dbdrnd_' + key, JSON.stringify(value))
}